import styled from 'styled-components';

export const LoadingBar = styled.div`
  height: 20px;
  background-color: #d8e4fc;
  margin-top: 8px;
  margin-right: 10px;
  float: left;

  &:nth-child(2n+1) {
    width: 350px;
  }

  &:nth-child(3n+2) {
    width: 78px;
  }

  &:nth-child(5n+3) {
    width: 169px;
  }

  &:nth-child(7n+5) {
    width: 310px;
  }

  &:nth-child(11n+7) {
    width: 283px;
  }
`;